<template>
    <div class="login">
        <div class="row">
            <div class="col s12 m10 offset-m1 l8 offset-l2 xl6 offset-xl3">
                <div class="card-panel hoverable">
                    <div class="switch right">
                        <label>
                        Acceso
                        <input type="checkbox" v-model="pin">
                        <span class="lever green"></span>
                        Registro
                        </label>
                    </div>
                    <h3 class="justificado">{{pin ? 'Registro' : 'Acceso'}}</h3>
                    <form @submit="enviar($event)">
                        <div>
                            <div class="row" v-if="pin">
                                <div class="input-field col s6">
                                    <input id="first_name" type="text" class="validate" v-model.trim="nombre">
                                    <label for="first_name" :class="[nombre != '' ? 'active' : '']">Nombre</label>
                                </div>
                                <div class="input-field col s6">
                                    <input id="last_name" type="text" class="validate" v-model.trim="apellido">
                                    <label for="last_name" :class="[apellido != '' ? 'active' : '']">Apellido</label>
                                </div>
                                <div class="input-field col s6">
                                    <input id="user_name" type="text" class="validate" v-model.trim="username">
                                    <label for="user_name" :class="[username != '' ? 'active' : '']">Usuario</label>
                                </div>
                                <div class="input-field col s6">
                                    <input id="dni" type="number" class="validate"  step="1" min=0 v-model.trim="dni">
                                    <label for="dni" :class="[dni != '' ? 'active' : '']">DNI</label>
                                </div>
                                <div class="input-field col s6">
                                    <input id="telephone" type="number" class="validate" step="1" min=0 v-model.trim="telefono">
                                    <label for="telephone" :class="[telefono != '' ? 'active' : '']">Telefono</label>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="input-field col s12">
                                <input id="email" type="email" class="validate" v-model.trim="correo">
                                <label for="email" :class="[correo != '' ? 'active' : '']">Email</label>
                            </div>
                        </div>
                        <div class="row">
                            <div class="input-field col s12">
                            <input id="password" type="password" class="validate" v-model.trim="pass1">
                            <label for="password" :class="[pass1 != '' ? 'active' : '']">Password</label>
                            </div>
                        </div>
                        <div class="row" v-if="pin">
                            <div class="input-field col s12">
                            <input id="password2" type="password" class="validate" v-model.trim="pass2">
                            <label for="password2" :class="[pass2 != '' ? 'active' : '']">Repita el Password</label>
                            </div>
                        </div>
                        <div class="row">
                            <div class="input-field col s12">
                            <input type="submit" :value="pin ? 'Registrar' : 'Acceder'" class="btn waves-effect green">
                            </div>
                        </div>
                    </form>
                </div>
            </div>
            
        </div>
        <div>
            <pre>
                {{$data}}
            </pre>
        </div>
    </div>
</template>

<script>
export default {
    name : "login",
    data : function(){
        return {
            pin : false,
            nombre : "",
            apellido : "",
            username : "",
            pass1 : "",
            pass2 : "",
            correo : "",
            telefono : "",
            dni : ""
        }
    },
    computed : {
        
    },
    methods : {
        enviar : function(event){
            event.preventDefault();
            if(this.validado()){
                console.log('Enviando....');
            } else {
                console.log('Hay un eror');
            }
            
        },
        validado : function(){
            let r = false;
            let mensaje = "";
            if(this.pin){
                r = (this.nombre != "") && (this.apellido != "") && (this.correo != "") && (this.username != "") && (this.correo != "") && (this.telefono != "") && (this.dni != "");
                mensaje = !r ? 'Todos los campos son obligatorios' : '';
                r = r && (this.pass1 == this.pass2) && (this.pass1 != "");
                mensaje += !((this.pass1 == this.pass2) && (this.pass1 != "")) ? ' Las contrase√±as no son iguales' : '';
            } else {
                r = (this.correo != "") && (this.pass1 != "");
                mensaje = !r ? 'Todos los campos son obligatorios' : '';
            }
            if(mensaje != ""){
                M.toast({html: mensaje});
            }
            return r;
        }
    }
}
</script>
